const initialState = {
	height: window.innerHeight,
  width: window.innerWidth,
  moveX: null,
  transDur: '0s',
}

export default (state = initialState, action) => {
	switch (action.type) {
		case 'UPDATE_MOVE_X':
			return Object.assign({}, state, { moveX: action.moveX });
		case 'UPDATE_DIMENSIONS':
			return Object.assign({}, state, {
				height: window.innerHeight,
				width: window.innerWidth
			});
		case 'MOVE_PAGE':
			return Object.assign({}, state, {
				moveX: action.moveX,
				transDur: '0s',
			});
		case 'ENTER_PAGE':
			return Object.assign({}, state, {
				moveX: action.moveX,
				transDur: '0.5s'
			});
		default: 
			return state;
	}
}


